import { Component, ViewChild, OnInit, ElementRef } from '@angular/core';
import { FormControl, FormGroup, Validators, NgForm } from '@angular/forms';
import { DatatableComponent } from "@swimlane/ngx-datatable";
import { HttpClientModule, HttpClient, HttpRequest, HttpResponse, HttpEventType } from '@angular/common/http';
import { TransactionsService } from '../shared/data/transactions.service';
import { Router, ActivatedRoute } from '@angular/router';
import { MembershipService } from '../shared/data/membership.service';

@Component({
  selector: 'app-transactions',
  templateUrl: './transactions.component.html',
  styleUrls: ['./transactions.component.scss']
})

export class TransactionsComponent implements OnInit {
  @ViewChild(DatatableComponent) table: DatatableComponent;
  private isLoading: boolean = false;
  dataSource = [];
  originalDataSource = [];
  filteredDataSource = [];
  members = [];
  search = {
    'paidUser' : '',
    'membership' : ''
  };
  pageSize: number = 10;
  columns = [
    { name: 'Name'},
    { name: 'Email'},
    { name: 'Membership plan'},
    { name: 'Membership plan Amount'},
    { name: 'Transaction Id'},
    { name: 'Renewal Due Date'},
    { name: 'Renewed'}
  ];
	constructor(public router: Router,
              private http: HttpClient,
              public transactionsservice: TransactionsService,
              public membershipService: MembershipService) {
      this.isLoading = true;
	}
  ngOnInit() {
    this.membershipService.getMembership().then(data => {
      if(data['membership']){
        this.members = data['membership'];
      }
    },
    error => {
    });
    this.transactionsservice.getTransaction().then(data => {
      if(data['status'] == "success"){
        this.dataSource = data['data'];
        this.originalDataSource = data['data'];
        this.isLoading = false;
      }
      else{
        this.dataSource = [];
        this.originalDataSource = [];
        this.isLoading = false;
      }
    });
	}

  filterTableOnPaidStatus(event){
    this.isLoading = true;
    if(event.target.value != ''){
      this.search.paidUser = event.target.value;
      let val = event.target.value.toLowerCase();
      if(this.search.membership != ''){
        this.dataSource = this.originalDataSource.filter(user => user.renewed_status.toLowerCase() === val);
        this.dataSource = this.dataSource.filter(user => user.mId=== this.search.membership);
      }
      else{
        this.dataSource = this.originalDataSource.filter(user => user.renewed_status.toLowerCase() === val);
        this.filteredDataSource = this.dataSource;
      }
      this.isLoading = false;
    }
    else{
      this.dataSource = this.originalDataSource;
      if(this.search.membership != ''){
        this.dataSource = this.originalDataSource;
        this.dataSource = this.dataSource.filter(user => user.mId=== this.search.membership);
      }
      this.search.paidUser = '';
      this.isLoading = false;
    }
    this.table.offset = 0;
  }

  filterTableOnMembership(event){
    this.isLoading = true;
    if(event.target.value != ''){
      let val = event.target.value;
      if(this.search.paidUser != ''){
        this.dataSource = this.originalDataSource.filter(user => user.mId === val);
        this.dataSource = this.dataSource.filter(user => user.renewed_status.toLowerCase() === this.search.paidUser.toLowerCase());
      }
      else{
        this.dataSource = this.originalDataSource.filter(user => user.mId === val);
        this.filteredDataSource = this.dataSource;
      }
      this.search.membership = val;
      this.isLoading = false;
    }
    else{
      this.dataSource = this.originalDataSource;
      if(this.search.paidUser != ''){
        this.dataSource = this.originalDataSource;
        this.dataSource = this.dataSource.filter(user => user.renewed_status.toLowerCase() === this.search.paidUser);
      }
      this.search.membership = '';
      this.isLoading = false;
    }
    this.table.offset = 0;
  }

  reset(){
    this.dataSource = this.originalDataSource;
    this.search.paidUser = '';
    this.search.membership = '';
    this.table.offset = 0;
  }
}
